.book{
    background-color: rgb(248, 243, 243);
    color: #111;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

.bookcontainer{
    max-width: 1300px;
    width: 800px;
    margin: auto;
    padding: 0px 20px;
}

.bsec{
    min-height: 100vh;
    /* display: flex;
    flex-direction: column;
    align-items: center; */
}

.bookname{
    font-size: 30px;
    line-height: 60px;
    text-align: center;
    margin-bottom: px;
}

.addbook{
    border: none;
    outline: none;
    background: #1d39c4;
    color: #fff;
    border-radius: 10px;
    width: 150px;
    height: 40px;
    /* display: flex; */
    justify-content: center;
    align-items: center;
    margin-top: px;
    margin-left: 640px;
    /* margin-right: -270px; */
    cursor: pointer;
    transition: .3s;
    border: 1px solid #1d39c4;
    font-size: 14px;
}

.bookcards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 40px;
}

.bookcard{
    /* flex: 0 0 calc(33.33% - 20px); */
    /* max-width: calc(33.33% - 20px); */
    width: 800px;
    height: 560px;
    background: #fff;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 30px;
    transition: .3s;
    border: 2px solid transparent;
    background-color: #ffff9e;
    align-items: center;
    justify-content: center;
    /* overflow-x: 'scroll'; */
}

.bookcard:hover{
    border-color: #1d39c4;
}

.bookcardname{
    font-size: 24px;
    line-height: 20px;
    text-align: center;
    margin-bottom: 40px;
    justify-content: center;
    align-items: center;
    font-weight: bolder;
    /* margin-right: px; */
}

.bpiccard{
    width: 240px;
    height: 240px;
    align-items: center;
    justify-content: center;
    display: flex;
    margin-left: 250px;
}

.booktext{
    margin-top: 30px;
    margin-bottom: 30px;
    margin-right: 30px;
    color: rgb(41, 4, 4);
    overflow: hidden;
    font-size: 20px;
    align-items: center;
    justify-content: space-between;
    z-index: 999;
    display: inline-block;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}

.blink{
    margin-bottom: 30px;
    margin-right: 30px;
    color: rgb(225, 20, 20);
    font-size: 22px;
}

